import logo from './logo.webp';
import { Link } from 'react-router-dom'

const Navbar = () => {
    return <div>
        <div className='Navbar'>
            <div className='Navbar-Logo-container'>
                <Link to="/Home">
                    <img src={logo} alt='logo' className='Navbar-Logo'/> 
                    <div className='Navbar-Logo-discription'>
                    TSOFJP
                    </div>
                </Link>
            </div>


            
            <div className='Title-Routes-container'>
                <div className='Title'>
                    
                </div>
                <div className='Routes'>
                    <div className='Route'>
                        <Link to="/AboutUs">
                            
                        </Link>
                    </div>
                    <div className='Route'>
                        <Link to="/Event">
                            
                        </Link>
                    </div>
                    <div className='Route'>
                        <Link to="/Blog">
                            
                        </Link>
                    </div>
                    <div className='Route'>
                        <Link to="/Contact">
                            
                        </Link>
                    </div>
                </div>

            </div>

        </div>

    </div>
}

export default Navbar